<template lang="pug">
    section.video-detail
        div.detail
            div.player(ref="player")
                //- 播放器
                video-player.svideo-player(
                    ref="videoPlayer"
                    :options="videoOptions"
                    @play="onPlayerPlay($event)"
                    @ready="playerReadied"
                )
            div.detail-main
                div.avator
                    router-link(:to="{ path: '/user/others', query: { to_user_id: info.user_id }}")
                        img(:src="info.head_image || ''")
                        img.icon-vip(:src="info.v_icon || ''" v-if="info.v_icon")
                div.info
                    div.title
                        span.name(v-text="info.nick_name || ''")
                        div.total
                            span.total-viewer
                                i.icon.iconfont &#xe690;
                                span(v-text="info.video_count || ''")
                            span.total-liker.liked(@click="like" v-if="info.has_digg == 1")
                                i.icon.iconfont &#xe697;
                                span(v-text="info.digg_count || ''")
                            span.total-liker(@click="like" v-else)
                                i.icon.iconfont &#xe696;
                                span(v-text="info.digg_count || ''")
                    div.sub-title(v-text="`视频简介:${info.content || ''}`")
            // div.detail-share
            //     label 分享至
            //     div
            //         span 微信好友
            //         span 朋友圈
        
        div.comment-list
            mt-loadmore(
                :top-method="loadTop"
                :bottom-method="loadBottom"
                :bottom-all-loaded="allLoaded"
                :autoFill="autoFill"
                ref="loadmore"
                @top-status-change="handleTopChange"
                @bottom-status-change="handleBottomChange"
            )
                mint-loadmore-top(:topStatus="topStatus" slot="top")
                div.list-block.media-list
                    ul(v-if="comment_list && comment_list.length")
                        li(v-for="item in comment_list")
                            div.item-content
                                div.item-media
                                    router-link(:to="{ path: '/user/others', query: { to_user_id: item.user_id }}")
                                        img(v-lazy="item.head_image" width=40)
                                div.item-inner(@click="showDelCommentSheet(item.user_id, item.comment_id)")
                                    div.item-title-row
                                        div.item-title(v-text="item.nick_name")
                                        div.after(v-text="item.left_time")
                                    div.item-subtitle
                                        span(v-text="item.content")
                        <div v-if="allLoaded" class="no-more-data">无更多数据</div>
                    null-data(v-else)
                mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded")
        div.float-comment
            div.float-comment-inner
                input(type="text" v-model="content" placeholder="请输入您的评论")
                span.btn-send-comment(@click="send") 发表评论
        mt-actionsheet(
            :actions="actions"
            v-model="sheetVisible"
        )      

</template>
<script>
import { videoPlayer } from 'vue-video-player'
import listBlock from '@/components/listBlock'
import listItem from '@/components/listItem'
import nullData from '@/components/nullData'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
import { mapGetters } from 'vuex'
import querystring from 'querystring'
export default {
    name: 'videoDetail',
    components: {
        listBlock,
        listItem,
        videoPlayer,
        mintLoadmoreTop,
        mintLoadmoreBottom,
        nullData
    },
    data() {
        return {
            info: {},
            comment_list: [],
            videoOptions: {},
            has_next: '',
            page: 2,
            content: '',
            sheetVisible: false,
            currentCommentId: '',
            autoFill: false,
            isLoaded: false,
            allLoaded: false,
            topStatus: '',
            bottomStatus: '',
        }
    },
    beforeRouteEnter: (to, from, next) => {
        next(vm => {

        });
    },
    created() {
        this.getData();
    },
    methods: {
        getData(isRefresh) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/weiboIndex' : this.api.weiboIndex();
            this.axios.get(axiosUrl, {
                params: {
                    weibo_id: this.$route.query.weibo_id
                }
            }).then(res => {
                if (res.data.status == 1) {
                    this.info = res.data.info;
                    this.comment_list = res.data.comment_list;

                    this.page = 2;
                    this.has_next = res.data.has_next;

                    this.$nextTick(function() {
                        isRefresh && this.$refs.loadmore.onTopLoaded();
                    });
                }
                else {
                    this.Toast(res.data.error || '操作失败');
                }
            })
                .catch(err => {
                    this.Toast(err || '网络异常');
                });
        },
        loadTop() {
            // 下拉刷新加载
            this.getData(true);
        },
        loadBottom() {
            // 无限加载
            let self = this;
            if (self.has_next) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/weiboIndex' : this.api.weiboIndex();
                self.axios.get(axiosUrl, {
                    params: {
                        weibo_id: this.$route.query.weibo_id,
                        page: self.page
                    }
                })
                    .then(res => {
                        setTimeout(() => {
                            if (res.data.status == 1) {

                                self.has_next = res.data.has_next;
                                self.page = self.page + 1;

                                if (res.data.comment_list && res.data.comment_list.length && res.data.comment_list.length > 0) {
                                    for (let i = 0; i < res.data.comment_list.length; i++) {
                                        self.comment_list.push(res.data.comment_list[i]);
                                    }
                                }
                            }
                            else {
                                this.Toast(res.data.error || '操作失败');
                            }
                            self.$refs.loadmore.onBottomLoaded();
                        }, 500);
                    })
                    .catch(err => {
                        this.Toast(err || '网络异常');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                setTimeout(function() {
                    self.$refs.loadmore.onBottomLoaded();
                }, 500);
                return false;
            }
        },
        send() {
            // 发表评论
            this.axios.post(this.api.userPublish_comment(), querystring.stringify({
                weibo_id: this.$route.query.weibo_id,
                content: this.content,
                type: 1
            }))
                .then(res => {
                    if (res.data.status == 1) {
                        this.Toast(res.data.error || '操作成功');
                        this.comment_list.unshift(res.data.comment);
                        
                        // 发表成功，清空输入框内容
                        this.$nextTick(() => {
                            this.content = '';
                        })
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '请求失败');
                });
        },
        showDelCommentSheet(user_id, comment_id) {
            if(user_id != this.userInfo.user_id)
                return;
            this.sheetVisible = ! this.sheetVisible;
            this.currentCommentId = comment_id;
        },
        delComment() {
            // 删除评论
            this.axios.post(this.api.delComment(), querystring.stringify({
                comment_id: this.currentCommentId
            }))
                .then(res => {
                    if (res.data.status == 1) {
                        this.Toast(res.data.error || '操作成功');
                        for(let key in this.comment_list) {
                            if(this.comment_list[key].comment_id = this.currentCommentId) {
                                this.comment_list.splice(key, 1);
                            }
                        }
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                });
        },
        like() {
            // 点赞
            this.axios.post(this.api.userPublish_comment(), querystring.stringify({
                weibo_id: this.$route.query.weibo_id,
                content: this.content,
                type: 2
            }))
                .then(res => {
                    if (res.data.status == 1) {
                        this.Toast(res.data.error || '操作成功');
                        this.info.digg_count = res.data.has_digg ? (parseInt(this.info.digg_count) + 1) : (parseInt(this.info.digg_count) - 1);
                        this.info.has_digg = res.data.has_digg;
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                });
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        },


        /**
         * @method :播放器监听事件
         * @param player
         */
        playerReadied(player) {
            // 播放器已初始化就绪
            // you can use it to do something...
            // player.[methods]

            $("video").attr({
                "x5-video-player-type": "h5",
                "x5-video-player-fullscreen": "true",
                "playsinline": !0
            });
        },
        onPlayerPlay(player) {
            // 触发播放
            this.axios.post(this.api.addVideoCount(), querystring.stringify({
                weibo_id: this.$route.query.weibo_id
            }))
                .then(res => {
                    if (res.data.status == 1) {
                        this.info.video_count = res.data.video_count;
                    }
                    else {
                        this.Toast(res.data.error || '操作失败');
                    }
                })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                });
        }
    },
    watch: {
        info() {

            let $obj = this.$refs.player;
            let win_w = $($obj).width(), win_h = $($obj).height();
            this.videoOptions = {
                // videojs and plugin options
                width: win_w,
                height: win_h,
                sources: [{
                    withCredentials: false,
                    type: 'video/mp4',
                    src: this.info.video_url
                }],
                autoplay: false,
                controls: true,
                controlBar: {
                    captionsButton: false,
                    chaptersButton: false,
                    playbackRateMenuButton: false,
                    LiveDisplay: true,
                    subtitlesButton: false,
                    currentTimeDisplay: true,
                    remainingTimeDisplay: true,
                    progressControl: true,
                    volumeMenuButton: {
                        inline: false,
                        vertical: true
                    },
                    fullscreenToggle: true
                },
                language: 'zh-CN',
                fullscreen: false,
                flash: { hls: { withCredentials: false } },
                html5: { hls: { withCredentials: false } },
                poster: this.info.photo_image
            }
        },
        comment_list() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        }),
        actions() {
            let self = this, arr = [{
                name: '删除',
                method: function() {
                    self.delComment();
                }
            }];
            return arr;
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../assets/css/variable.less';
.svideo-player {
    width: 100%;
    height: 100%;
}

.video-detail {
    padding-bottom: 2.5rem;
}

.player {
    width: 100%;
    height: 56vw;
    img {
        width: 100%;
        height: 100%;
    }
}

.detail-main {
    padding: .5rem;
    display: flex;
    align-items: center;
    background-color: #fff;
    .avator {
        position: relative;
        width: 2.1rem;
        height: 2.1rem;
        margin-right: .5rem;
        flex-shrink: 0;
        img {
            width: 100%;
            height: 100%;
            border-radius: 2.1rem;
        }
        .icon-vip {
            position: absolute;
            bottom: 0;
            right: 0;
            width: .6rem;
            height: .6rem;
        }
    }
    .info {
        width: 100%;
    }
    .title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .name{
            width: 65%;
            display: block;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
        }
        .total {
            font-size: .6rem;
            color: @fc-light;
            .total-viewer {
                margin-right: .5rem;
            }
            .iconfont {
                font-size: .6rem;
                margin-right: .25rem;
            }
            .total-liker{
                &.liked{
                    color: @color-theme;
                }
            }
        }
    }
    .sub-title {
        font-size: .55rem;
        color: @fc-light;
    }
}

.detail-share {
    display: flex;
    justify-content: space-between;
    padding: .5rem;
    background-color: #fff;
    border-top: 1px solid @bdcolor;
}

.comment-list {
    .list-block {
        .item-content {
            padding-left: .5rem;
            align-items: flex-start;
        }
        .item-inner {
            margin-left: .5rem;
            padding-right: .5rem;
            flex-wrap: wrap;
        }
    }
    .list-block .item-subtitle {
        font-size: .55rem;
        color: @fc-light;
        white-space: normal;
        .nick-name {
            color: @color-red;
        }
    }
    .list-block.media-list .item-media {
        width: 2rem;
        height: 2rem;
        position: relative;
        padding: 0;
        margin: .5rem 0;
        img {
            border-radius: 10rem;
        }
    }
    .after {
        font-size: .55rem;
        color: @fc-light;
    }
}

.float-comment {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    background-color: #fff;
    .float-comment-inner {
        width: 100%;
        padding: .5rem;
        display: flex;
        align-items: center;
        .btn-send-comment {
            height: 1.5rem;
            line-height: 1.5rem;
            padding: 0 .75rem;
            background-color: @color-theme;
            color: #fff;
            border-radius: 3px;
            flex-shrink: 0;
            margin-left: 0.5rem;
        }
        input {
            width: 100%;
            height: 1.5rem;
            border: 0;
            background-color: #f3f3f3;
            border-radius: 3px;
            padding: 0 .5rem;
        }
    }
}
</style>


